<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add_Skill') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body"> 
                        <dynamic-form lang="en" ref="IndustryForm" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        AddIndustryObj: {
            type: Object,
            required: true
        }
    },
    data(){
        return{
            isLoading:false,
            FormSchema:[
            {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('Industry'),
                            name: "Industry",
                            value: "",
                            placeholder: this.$t('Enter Industry'),
                            validationRules: {
                                "required": true
                            },                         
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Icon'),
                            name: "Icon",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": false},
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('Enter Description'),
                            validationRules: {
                                "required": false
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,                    
                   
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId:null,
            userId:null
        }
    },
    created:function(){        
        var vm=this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.GetIndustryIcons();
        if(vm.AddIndustryObj != null){            
            vm.FormSchema[0].Data[0].value=vm.AddIndustryObj.crm_lead_industry_type;
            vm.FormSchema[1].Data[0].value=vm.AddIndustryObj.crm_lead_industry_type_desc;
        }
    },
    methods:{
        GetIndustryIcons:function(){            
            var vm=this;
            var url=`id=`;
            DataService.GetIndustryIcons(url).then((response)=>{                 
                if(response.data.DATA.length>0){
                    var icondata=response.data.DATA;
                    icondata.forEach(element => {
                        let obj={
                            value:element.icon_value,
                            name:element.icon_name
                        }                        
                        vm.FormSchema[0].Data[1].config.options.push(obj);
                        vm.$refs.IndustryForm.UpdateKeyValue();   
                    });
                }
            })
        },
        onSubmit(formfields, val){            
            var vm = this;
            var jsonobj = {
                userId:vm.userId,
                companyId:vm.companyId,
                name: formfields.Industry,
                description: formfields.Description,
                icondata: formfields.Icon,
                parentid: null,
                id:vm.AddIndustryObj !=null?vm.AddIndustryObj.crm_lead_industry_type_id:0
            };            
            var params = JSON.stringify(jsonobj);
            DataService.SaveIndustry(params).then((response)=>{  
                if (response.data.status == 'AlreadyExixts') {                    
                    vm.ShowAlert(vm.$t('IndustryAlreadyExist', [vm.$t('Industry')]), "warning", true, vm.$t("Alert"));
                }              
                if (response.data.status == "Success") {                                        
                    if(vm.AddIndustryObj !=null){
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Industry')]), "success", true, vm.$t("Alert"));
                    }else{
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Industry')]), "success", true, vm.$t("Alert"));
                    }                        
                    vm.$parent.FetchData();
                    vm.Close();                    
                }
            });

        },
        Close:function(){
            this.$parent.isAddIndustry=false;
        }
        
    }
}
</script>