<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add_Skill') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        SkillId: {
            type: Object,
            required: true
        }
    },
    data(){
        return{
            isLoading:false,
            FormSchema:[
            {
                    layoutType: "single",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('Skill'),
                            name: "Skill",
                            value: "",
                            placeholder: this.$t('Enter Skill'),
                            validationRules: {
                                "required": true
                            },                         
                            disabled: false,
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('Enter Description'),
                            validationRules: {
                                "required": false
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true,                    
                   
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            companyId:null,
            userId:null
        }
    },
    created:function(){        
        var vm=this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if(vm.SkillId != null){            
            vm.FormSchema[0].Data[0].value=vm.SkillId.title;
            vm.FormSchema[1].Data[0].value=vm.SkillId.description;
        }
    },
    methods:{
        onSubmit(formfields, val){            
            var vm = this;
            var jsonobj = {
                userId:vm.userId,
                companyId:vm.companyId,
                name: formfields.Skill,
                description: formfields.Description,
                id:vm.SkillId !=null?vm.SkillId.skill_id:0
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveSkill(params).then((response)=>{                
                if (response.data.Status == "Success") {
                    if (response.data.Code == 0) {                    
                        vm.ShowAlert(vm.$t('SkillAlreadyExist', [vm.$t('Skill')]), "warning", true, vm.$t("Alert"));
                    }else{
                        if(vm.SkillId !=null){
                            vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Skill')]), "success", true, vm.$t("Alert"));
                        }else{
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Skill')]), "success", true, vm.$t("Alert"));
                        }                        
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                }
            });

        },
        Close:function(){
            this.$parent.isAddSkill=false;
        }
        
    }
}
</script>