<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddTag') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        TagArr: {
            type: Object,
            required: true
        },
        TagId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Tag'),
                            name: "Tag",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
            if(vm.TagId>0){
                (vm.FormSchema)[0].Data[0].value = vm.TagArr.row.tag_name;
                (vm.FormSchema)[1].Data[0].value = vm.TagArr.row.tag_desc;
            }
        },
        Close: function () {
            this.$parent.AddTagPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                TagId: vm.TagId == null ? 0 : vm.TagId,
                tagName: formfields.Tag,
                tagDesc: formfields.Description,
                tagStatusId:1001,
                moduleName:'Hiring'
            };
            var params = JSON.stringify(jsonobj);
            DataServices.Savetag(params).then((response) => {
                if (response.data[0].Status == 'Success') {
                        if (vm.TagId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Tag")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Tag")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Tag")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>