<template>
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('Manage_Skill') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                    
                </div>
                <div class="modal-body">
                    <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="Area" :LegendArray="[]"
                            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                            :widgets="[]" IdentityColumn="skill_id" :listType="['List']" :ListData="SkillArr"
                            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                            :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'title'">
                                    <a href="#" @click="openAddSkill(data.row)">{{
                                        data.row.title }}</a>
                                </template>
                            </template>
                        </tg-list>
                </div>
            </div>
        </div>
    </div>
    <AddSkill v-if="isAddSkill" :SkillId="SkillId"></AddSkill>
</div>
</template>
<script>
import DataServices from './../../../services/DataService'
import Schema from './SkilSchema';
import AddSkill from './AddSkillPop.vue'
export default {
    props: {
        SField: {
            type: Object,
            required: true
        },
        SFieldId: {
            type: Object,
            required: true
        }
    },
    components:{AddSkill},
    data(){
        return{
            isLoading:false,
            HeaderText:'',
            isAddSkill:false,
            listheaderbuttons: Schema.buttons,
            Headers:[{
                "COLUMN_NAME": "title",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Skill Type',
                "DISPLAY_ORDER": 1,
                settings : {                            
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Skill Description',
                "DISPLAY_ORDER": 2
            }], 
            SortBy:'',          
            skillStringArr:[],
            SortExp:'',
            TotalRecords: 0,
            searchCondition:'',
            PageNumber: 1,
            PageSize:10,         
            SkillArr: [],           
            CurrentPage: 1,
            TotalPages: 0,    
            skillString: '',
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"skillname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            SkillId:null
        }
    },
    created:function(){
        var vm=this;
        vm.FetchData();
    },
    methods:{
        renderActions: function (row) {
           
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddSkill":
                    this.openAddSkill();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddSkill:function(item){            
            var vm=this;            
            vm.SkillId=item == undefined?null: item;      
            vm.isAddSkill=true;
        },        
        commonDeleteData: function (id) {            
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {                   
                    var url=`ids=${val}&tablename=TALYGEN_PR_skill&key=skill_id`;                 
                    DataServices.DeleteItems(url).then(function (response) {                        
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Skill')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Skill')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData:function(){            
            var vueObj = this;
            vueObj.isLoading = true;
            if(vueObj.searchCondition !=''){
                var params = `pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&${vueObj.searchCondition}`;
            }else{
                var params = `skillname=&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            }            
            DataServices.ManageSkills(params).then((response) => {    
                if(response.data && response.data.DATA.length>0){
                    vueObj.SkillArr = response.data.DATA;
                    vueObj.TotalRecords = response.data.DATA[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;   
                    vueObj.listheaderbuttons[1].isdisabled = true;                 
                }else{
                    vueObj.noRecord = false;
                    vueObj.SkillArr = [];
                    vueObj.TotalRecords = 0
                }
                vueObj.isLoading = false;
            })
        },
        Close:function(){
            this.$parent.GetDropDownData("HR_SKILL", this.SField, this.SFieldId, null);   
            this.$parent.Isopenaddskillpopup=false;
        }
    }
}
</script>


