<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Manage_Tag') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                        <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="Area" :LegendArray="[]"
                            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                            IdentityColumn="tag_id" :listType="['List']" :ListData="TagData" :HeaderData="Headers"
                            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData"
                            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
                            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                            v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'tag_name'">
                                    <a href="#" @click="openAddTags(data)">{{
                                        data.row.tag_name }}</a>
                                </template>
                            </template>
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
        <AddTagPopup v-if="AddTagPopup" :TagArr="TagArr" :TagId="TagId"></AddTagPopup>
    </div>
</template>
<script>
import DataServices from './../../../services/DataService'
import Schema from './SkilSchema'
import AddTagPopup from './AddTagPopup.vue'
export default {
    props: {
        TField: {
            type: Object,
            required: true
        },
        TFieldId: {
            type: Object,
            required: true
        }
    },
    components: { AddTagPopup },
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            TagId:0,
            TagArr:[],
            AddTagPopup: false,
            listheaderbuttons: Schema.buttons,
            Headers: [{
                "COLUMN_NAME": "tag_name",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Tag Type',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "tag_desc",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Tag Description',
                "DISPLAY_ORDER": 2
            }],
            SortBy: '',
            skillStringArr: [],
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            TagData: [],
            CurrentPage: 1,
            TotalPages: 0,
            skillString: '',
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "tagname",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            noRecords:false,
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddSkill":
                    this.openAddTags();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tag")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_tag&key=tag_id`;
                    DataServices.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Tag')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Tag')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        openAddTags: function (item) {
            var vm =this;
            if (item == undefined){
                vm.TagId=0;
                vm.TagArr = [];
            }else{
                vm.TagId=item.row.tag_id;
                vm.TagArr = item;
            }
            vm.AddTagPopup = !vm.AddTagPopup;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: async function () {
            var vueObj = this;
            if (vueObj.searchCondition != '') {
                var params = `pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&${vueObj.searchCondition}`;
            } else {
                var params = `tagname=&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            }
            await DataServices.ManageTags(params).then((response) => {
                vueObj.TagData=[];
                if (response.data && response.data.DATA.length > 0) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "tag_name") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.TagData = response.data.DATA;
                    if (vueObj.TagData.length > 0) {
                        vueObj.TotalRecords = response.data.DATA[0].TotalRecords;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecords = false;
                    }
                    else {
                        vueObj.noRecords = true;
                    }
                }
                else {
                    vueObj.noRecords = true;
                    vueObj.TotalRecords = 0
                }
            })
        },
        Close: function () {
            this.$parent.GetDropDownData("HR_TAG", this.TField, this.TFieldId, null);   
            this.$parent.Isopenaddtagpopup = false;
        }
    }
}
</script>
    
    
    