<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div id="divMainLeadData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white" v-if="reqfrom=='HR/CandidateList'">{{ $t('AddCandidate') }}</span>
                        <span class="p-name text-white" v-else="reqfrom=='HR/TalentPoolList'">{{ $t('AddTalentPool') }}</span> 
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')"
                                @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="form">
                    <dynamic-form ref="CandidateForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
                        <template v-slot:tgslot-OverallExperienceGroup="{ data }">                                                     
                            <div class="row" v-for="(item, index) in ExpData" :key="index">
                                <div class="col-lg-12 mt-4">
                                    <div class="heading-border mb-2">
                                        <h5 class="h5">
                                            <span>{{ $t('OverallExperience') }}</span><small class="float-right">
                                                <a href="javascript:;" v-on:click="AddMoreExp"
                                                    :title="$t('AddMore')"><em class="fa fa-plus pr-2"></em>{{
                                                    $t('AddMore') }}</a>
                                            </small>
                                        </h5>
                                    </div>
                                </div>
                                <div class="col-lg-12">
                                    <div class="col-lg-12 text-right ml-3">
                                        <a href="javascript:;" class="btn btn-danger" title="Delete"
                                            v-on:click="DeleteExpRow(item, index)" v-if="index != 0"><em
                                                class="fa fa-trash text-white pr-2"></em>Delete</a>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('Occupation') }}:</label>
                                        <Field :name="'Occupation'" label="Occupation">
                                            <input type="text" v-model="item.txtoccupation"
                                                v-bind:class="{ 'form-control': true }"
                                                v-bind:name="'Occupation' + index">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('CompanyName') }}:</label>
                                        <Field :name="'CompanyName'"  label="Company Name">
                                            <input type="text" v-model="item.txtcompname"
                                                v-bind:class="{ 'form-control': true }"
                                                v-bind:name="'CompanyName' + index">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <Field :name="'date' + item + index"  label="From"
                                        v-slot="{ errors }">
                                        <label>{{ $t('From') }}:</label>
                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                            class="input-group" v-model="item.FromDate"
                                            v-bind:name="'date' + item + index"
                                             v-bind:value="item.FromDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </Field>
                                </div>                               
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4" v-if="item.Workshere == 0">
                                    <Field :name="'date' + item + index"  label="To"
                                        v-slot="{ errors }">
                                        <label>{{ $t('To') }}:</label>
                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                            class="input-group" v-model="item.ToDate"
                                            v-bind:name="'date' + item + index"
                                            v-bind:value="item.ToDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12">
                                    <div class="form-group">
                                        <label>Summary:</label>
                                        <div class="row">
                                            <div class="col-12 float-left">
                                                <div>
                                                    <ckeditor :editor="editor" @ready="onReady" v-model="item.summary">
                                                    </ckeditor>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>                            
                                <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 mt-3">                                   
                                    <div class="form-group">
                                        <label>Currently works here:</label>
                                        <div class="form-group">
                                            <label class="switch">
                                                <input type="checkbox" class="form-check-input rdoticketfor custom-control-input dynamic" @change="changeButton(index)" :name="item.name" :checked="item.Workshere==1"> 
                                                <span class="slider round">
                                                    <span class="slider-yes">Yes</span> 
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>
                        <template v-slot:tgslot-EduInfo="{ data }">
                            <div class="row" v-for="(item,Eindex) in EduInfoData" :key="Eindex">
                                <div class="col-lg-12 mt-4">
                                    <div class="heading-border mb-2">
                                        <h5 class="h5">
                                            <span>{{ $t('EducationInfo') }}</span><small class="float-right">
                                                <a href="javascript:;" v-on:click="AddMoreEduInfo"
                                                    :title="$t('AddMore')"><em class="fa fa-plus pr-2"></em>{{
                                                    $t('AddMore') }}</a>
                                            </small>
                                        </h5>
                                    </div>
                                </div>
                                <div class="col-lg-12 ">
                                    <div class="col-lg-12 text-right ml-3">
                                        <a href="javascript:;" class="btn btn-danger"
                                            v-on:click="DeleteEduInfoRow(item, Eindex)" v-if="Eindex != 0"
                                            :title="$t('Delete')"><em class="fa fa-trash text-white pr-2"></em>{{
                                            $t('Delete') }}</a>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('Degree') }}:</label>
                                        <Field :name="'degree'"  label="Occupation">
                                            <input type="text" v-model="item.txtdegree"
                                                v-bind:class="{ 'form-control': true }" v-bind:name="'degree' + Eindex">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('Institute') }}:</label>
                                        <Field :name="'degree'"  label="Institute">
                                            <input type="text" v-model="item.txtinstitute"
                                                v-bind:class="{ 'form-control': true }"
                                                v-bind:name="'Institute' + Eindex">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('FieldStudy') }}:</label>
                                        <Field :name="'FieldStudy'" label="FieldStudy">
                                            <input type="text" v-model="item.txtfieldstudy"
                                                v-bind:class="{ 'form-control': true }"
                                                v-bind:name="'FieldStudy' + Eindex">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <div class="form-group">
                                        <label>{{ $t('Grade') }}:</label>
                                        <Field :name="'grade'" label="grade">
                                            <input type="text" v-model="item.txtgrade"
                                                v-bind:class="{ 'form-control': true }" v-bind:name="'grade' + Eindex">
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4">
                                    <Field :name="'date' + item + Eindex"  :label="item + Eindex"
                                        v-slot="{ errors }">
                                        <label>{{ $t('From') }}:</label>
                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                            class="input-group" v-model="item.EduFromDate"
                                            v-bind:name="'date' + item + Eindex" 
                                            :value="item.EduFromDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 float-left col-lg-4 col-xl-4" v-if="item.showEduTodate">
                                    <Field :name="'date' + item + Eindex"  :label="item + Eindex" v-slot="{ errors }">
                                        <label>{{ $t('To') }}:</label>
                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks" class="input-group"
                                            v-model="item.EduToDate" v-bind:name="'date' + item + Eindex"
                                             v-bind:value="item.EduToDate">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </Field>
                                </div>                                                    
                                <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 mt-3">                                   
                                    <div class="form-group">
                                        <label>Pursuing:</label>
                                        <div class="form-group">
                                            <label class="switch">
                                                <input type="checkbox" class="form-check-input rdoticketfor custom-control-input dynamic" @change="changePursuebutton(Eindex)" :name="item.name" :checked="item.pursue==1"> 
                                                <span class="slider round">
                                                    <span class="slider-yes">Yes</span> 
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>
                        <template v-slot:tgslot-AttachFile="{ data }">
                            <div class="row">
                                <div class="col-lg-12 mb-3">
                                    <div class="heading-border mb-2">
                                        <h5 class="h5">
                                            <span>Attach Documents</span><small class="float-right">
                                                <a href="javascript:;" v-on:click="MultipleDocument"
                                                    :title="$t('AddMore')"><em class="fa fa-plus pr-2"></em>{{
                                                    $t('AddMore') }}</a>
                                            </small>
                                        </h5>
                                    </div>
                                </div>
                            </div>
                            <div :id="'docs' + Iindex" v-for="(item, Iindex) in AttachmentData" class="col-lg-12 px-0"
                                :key="Iindex">
                                <div class="col-lg-4 float-left">
                                    <div class="form-group">
                                        <div class="row">
                                            <div class="col-11 col-sm-9 float-left">
                                                <label>DocumentType:</label>
                                                <Field :name="'field_name' + (Iindex + 1)"
                                                    v-bind:data-vv-name="'SearchFilter.fieldName' + (Iindex + 1)"
                                                    label="Field Name">
                                                    <select class="form-control" v-model="item.TypeName" name="docs">
                                                        <option value="">Select</option>
                                                        <option v-for="option in documenttypes" v-bind:value="option.Id"
                                                            :key="option">
                                                            {{ option.doc_type }}
                                                        </option>
                                                    </select>
                                                </Field>
                                            </div>
                                            <a class="round-icon-small btn-dark mt-4">
                                                <em class="fa fa-wrench text-white"
                                                    v-on:click="openmanagedoument()"></em>
                                            </a>
                                            <div class="col-sm-12 col-md-12 float-left mb-2">
                                                <small class="form-text text-muted is-invalid">{{ $t('AllowedFileTypes')
                                                    }}:{{ AllowedFileTypes }}</small>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-lg-4 float-left">
                                    <div class="form-group">
                                        <label>File Name:</label>
                                        <div class="row ">
                                            <div class="col-lg-10 float-left">
                                                <div class="input-group">
                                                    <div class="custom-file">
                                                        <input type="file" class="custom-file-input"
                                                            v-bind:name="'item_image' + Iindex"
                                                            v-bind:id="'item_image' + Eindex"
                                                            v-on:change="BindAttachement($event, Iindex)" />
                                                        <label class="custom-file-label"
                                                            v-bind:for="'item_image' + index">{{ (item.document_name !=
                                                            null && item.document_name !=
                                                            "" ? getFileName(item.document_name) : 'Choose file'
                                                        ) }}</label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-lg-2 d-flex align-items-center">
                                                <a href="javascript:;" v-on:click="DeleteAttachment(item, Iindex)"
                                                    title="Delete" v-if="(Iindex != 0)" class="bg-transparent p-0"
                                                    style="font-size:18px;"><em
                                                        class="fa fa-trash text-danger"></em></a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>
                        <template v-slot:tgslot-star_rating="{ data }">
                            <div class="givenrating" v-on:click="StarCheck($event)">
                                <span id="1"
                                    v-bind:class="{ 'fa fa-star checked': rating >= 1 && rating != 0, 'fa fa-star': rating < 1 || rating == 0 }"></span>
                                <span id="2"
                                    v-bind:class="{ 'fa fa-star checked': rating >= 2 && rating != 0, 'fa fa-star': rating < 2 || rating == 0 }"></span>
                                <span id="3"
                                    v-bind:class="{ 'fa fa-star checked': rating >= 3 && rating != 0, 'fa fa-star': rating < 3 || rating == 0 }"></span>
                                <span id="4"
                                    v-bind:class="{ 'fa fa-star checked': rating >= 4 && rating!=0,'fa fa-star':rating<4 || rating == 0}"></span>
                                <span id="5"
                                    v-bind:class="{'fa fa-star checked':rating>=5 && rating!=0,'fa fa-star':rating<5 || rating == 0}"></span>
                            </div>
                        </template>
                    </dynamic-form>
                </Form>
            </div>
        </div>
        <SkillPopup v-if="Isopenaddskillpopup" :SField="SField" :SFieldId="SFieldId"></SkillPopup>
        <AreaPopup v-if="Isopenaddareapopup" :AField="AField" :AFieldId="AFieldId" :IsIndustryTypeId="IsIndustryTypeId" :key="reportcomponentkey"></AreaPopup>
        <IndustrayPopup v-if="IsopenaddIndustrypopup" :IField="IField" :IFieldId="IFieldId"></IndustrayPopup>
        <TagPopup v-if="Isopenaddtagpopup" :TField="TField" :TFieldId="TFieldId"></TagPopup>
        <DocumentPopup v-if="Isopenadddocumentpopup"></DocumentPopup>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import SkillPopup from './SkillPopUp.vue'
import AreaPopup from './AreaPopUp.vue'
import IndustrayPopup from './IndustrayPoPUp.vue'
import TagPopup from './TagPopUp.vue'
import DocumentPopup from './DocumentPopup.vue'
import moment from 'moment'
export default {
    components: {
        SkillPopup, AreaPopup, IndustrayPopup, TagPopup, DocumentPopup
    },
    data() {
        return {
            isLoading: false,
            reportcomponentkey:0,
            SField: [],
            SFieldId: 0,
            TField: [],
            TFieldId: 0,
            IField: [],
            IFieldId: 0,
            AField: [],
            AFieldId: 0,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            PageNumber: 1,
            PageSize: 10,
            JobsData: [],
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            documenttype: [],
            AttachmentData: [],
            documenttypes: [],
            ExpData: [],
            EduInfoData: [],
            OverallExperienceForm: [],
            EduInfoForm: [],
            FormSchema: [],
            RecordId: 0,
            rating: 0,
            id: 0,
            ddljobs: 0,
            userId: null,
            companyId: null,
            ContainerName: null,
            sourcename: null,
            TotalFiles: '',
            PerFileSize: '',
            TotalFilesSize: '',
            AllowedFileTypes: '',
            attachement_mime: '',
            fileName: '',
            base64String: '',
            item_type: 'doc',
            reqfrom: '',
            Isopenaddskillpopup: false,
            popover: {
                visibility: 'click'
            },
            masks: {
                input: 'MM-DD-YYYY',
                CompanyType: ''
            },
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            Isopenaddtagpopup: false,
            IsopenaddIndustrypopup: false,
            Isopenaddareapopup: false,
            IsIndustryTypeId: null,
            Isopenadddocumentpopup: false,
            pid: null,
            aid: null,
            isRedirect: '',
            CvData:[],
            fileNameProfile:'',
            attachement_mimeProfile:'',
            attachement_mimeProfile:'',
            tagvalue:0,
            ddlDocTypes:'',
            base64StringProfile:''
        }
    },
    created: async function () {        
        var vm = this;
        await vm.DocumentTypes();
        vm.RecordId = vm.$route.params.id == '0' ? 0 : vm.$route.params.id;
        vm.pid = vm.$route.params.pid;
        vm.aid = vm.$route.params.aid;
        await vm.DocumentTypes();
        vm.isRedirect = vm.$route.params.uid != null ? true : false;
        vm.reqfrom = vm.$route.params.reqfrom;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.ContainerName = vm.GetUserInfo.ContainerName;
        vm.sourcename = vm.GetUserInfo.UserType == 'CA' ? 'Admin' : vm.GetUserInfo.UserType == 'PM' ? 'Manager' : 'Client';
        await vm.FetchData();       
        await vm.GetJobs();
        await vm.GetDocTypes();
        await vm.GetConfigurationValueByKey();
    },
    methods: {
        openaddskillpopup: function () {
            this.Isopenaddskillpopup = true;
        },
        openaddtagpopup: function () {
            this.Isopenaddtagpopup = true;
        },
        openmanagedoument: function () {
            this.Isopenadddocumentpopup = true;
        },
        openaddIndustrypopup: function () {
            this.IsopenaddIndustrypopup = true;
        },
        openaddareapopup: function () {
            var vm = this;
            if (vm.IsIndustryTypeId == null) {
                vm.ShowAlert(vm.$t("PleaseSelectIndustryBefore"), "warning", true, vm.$t("Alert"));
            } else {
                vm.reportcomponentkey += 1;
                vm.Isopenaddareapopup = true;
            }
        },
        GetConfigurationValueByKey: async function () {
            var vm = this;
            var params = `moduleName=JobPost&configkey=`;
            DataService.GetConfigurationValueByKey(params).then(function (response) {
                if (response.data) {
                    vm.TotalFiles = response.data[0].CONFIG_DATA_VALUE;
                    vm.PerFileSize = response.data[1].CONFIG_DATA_VALUE;
                    vm.TotalFilesSize = response.data[2].CONFIG_DATA_VALUE;
                    vm.AllowedFileTypes = response.data[3].CONFIG_DATA_VALUE;
                    vm.FormSchema[0].Data[0].config.maxSize = response.data[1].CONFIG_DATA_VALUE;
                }
            })

        },
        StarCheck: function (event) {
            if (event != null && event.target != null) {
                this.rating = event.target.id
            }
        },
        GetJobs: async function () {            
            var vm = this;
            var params = `ids=&moduleName=&type=JOB_POST&search=&refId=`
            DataService.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[0].Data[1].config.options = response.data.DATA;
                }
                vm.$refs.CandidateForm.UpdateKeyValue();
            })
        },
        GetDocTypes: async function () {
            var vm = this;
            const params = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            DataService.DocumentTypes(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.documenttype = response.data.DATA;
                    vm.TotalRecords = vm.documenttype[0].TotalRecords;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                }
                else {
                    vm.noRecord = true;
                    vm.TotalRecords = 0
                }
            })
        },
        DocumentTypes: async function () {
            var vm = this;
            var url = `id=`;
            DataService.GetDocumentTypes(url).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.documenttypes = response.data.DATA;
                }
            })

        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        BindAttachement: function (evt, index) {            
            var vm = this;
            vm.isLoading = true;
            var f = evt.target.files[0];
            var reader = new FileReader();
            if (f != undefined) {
                reader.onload = (function () {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        vm.AttachmentData[index].base64String = base64StringEncoded;
                        vm.AttachmentData[index].fileName = f.name;
                        vm.AttachmentData[index].document_name = f.name;
                        vm.AttachmentData[index].attachement_mime = attachemnetMime;
                        vm.AttachmentData[index].containerName = vm.ContainerName;
                        vm.AttachmentData[index].userId = vm.userId,
                            vm.AttachmentData[index].moduleName = "HIRING",
                            vm.AttachmentData[index].subModuleCode = "HR_TALENT_POOL",
                            vm.AttachmentData[index].companyId = vm.companyId,
                            vm.isLoading = false;
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            vm.isLoading = false;
        },
        getFileName: function (pth) {
            var arr = pth.split('\\');
            return arr[arr.length - 1];
        },
        DeleteAttachment: function (item, index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteField"), true, 'Confirm', function () {
                vm.AttachmentData.splice(index, 1);
                vm.PlusIcon = false;
                vm.ddlDocTypes = '';
                $('#docs' + index).remove();
            });
        },
        DeleteExpRow: function (item, index) {
            var vm = this;
            if (index === 0) {
                vm.confirmR(vm.$t("ConfirmDeleteField"), true, 'Confirm', function () {
                    this.ExpData();
                });
            }
            else {
                var vm = this;
