        GetBase64StringCv: function (evt) { 
            var vm = this;
            vm.isLoading = true;
            var f = evt.target.files[0];
            var reader = new FileReader();
            if (!this.CheckResumeFileExtension(f.name.toLowerCase())) {
                reader.onload = (function (theFile) {
                    return function (e) {                        
                        var binaryData = '';
                        var base64StringEncoded = '';
                        var fileName = '';
                        var n = '';
                        var extensionName = '';
                        var attachemnetMime = '';
                        fileName = '';
                        vm.attachement_mime = '';
                        vm.fileName = '';
                        vm.base64String = '';
                        vm.isLoading = false;
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            else {
                reader.onload = (function () {
                    return function (e) {   
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        fileName = f.name;
                        vm.attachement_mime = attachemnetMime;
                        vm.fileName = fileName;
                        vm.base64String = base64StringEncoded;
                        vm.isLoading = false;                        
                    };
                })(f);
                reader.readAsBinaryString(f);               
            }
            vm.isLoading = false;
            vm.OnFileChange(evt);
        },
        OnFileChange:function(event){            
            var vm=this;
            var data = new FormData();
            data.append("FileObject", event.target.files[0]);
            DataService.ParsePdf(data).then((response)=>{                
                if(response.data !=''){                    
                    var jsonData = JSON.parse(response.data);
                    var jsonDataFields = jsonData[0].fields;                    
                    if (jsonDataFields != "null") {
                        $.each(jsonDataFields, function (k, v) {  
                            vm.FormSchema.forEach((group, gi) => {
                                group.Data.forEach(async (field, fi) => {
                                    if (k.toString() == 'name') {
                                        var fullName = v.toString().split(" ");
                                        for (let i = 0; i < fullName.length; i++) {                                            
                                            if (i == 0) {
                                                if (field.name == "first_name") {
                                                    field.value=fullName[i];
                                                }
                                            }
                                            else if (i == 1 && fullName.length == 2) {
                                                if (field.name == "last_name") {
                                                    field.value=fullName[i];
                                                }
                                            }
                                            else if (i == 1 && fullName.length > 2) {
                                                if (field.name == "middle_name") {
                                                    field.value=fullName[i];
                                                }
                                            }                                            
                                        }                                
                                    }
                                    if (k.toString() == "email") {                                        
                                        if (field.name == "email_id") {
                                            field.value=v;
                                        }
                                    }
                                    if (k.toString() == "mobile_number") {                                        
                                        if (field.name == "mobile") {
                                            field.value=v;
                                        }
                                    }
                                    if (k.toString() == "education") {                                        
                                        vm.EduInfoData[0].txtdegree = "";
                                        vm.EduInfoData[0].txtfieldstudy = "";
                                        if (v != null) {
                                            var educationData = v.toString().split(" in ");
                                            vm.EduInfoData[0].txtdegree = educationData[0];
                                            vm.EduInfoData[0].txtfieldstudy = educationData[1];
                                        }
                                    }
                                    if (k.toString() == 'skills') {                                               
                                        let trimmedV = v.replace(/,\s+/g, ',');                                
                                        var values = trimmedV.split(',').filter(Boolean);
                                        if (field.name == "skill_id") {                                            
                                            var filteredOptions = field.config.options.filter(option => values.includes(option.name));
                                            var mappedOptions = filteredOptions.map(option => ({ label: option.name, value: option.value }));
                                            field.value = mappedOptions;
                                        } 
                                    }
                                })
                            })   
                        })
                    }
                    
                }
            })
        },
