        GetBase64StringProfile: function (evt) {            
            var vm = this;
            if (evt[0] == undefined) {
                return;
            }
            vm.isLoading = true;
            const file = evt[0];
            const fileName = file.name.toLowerCase()
            var extension = fileName.replace(/^.*\./, '');
            if (extension === fileName) {
                extension = '';
            }
            else {
                extension = extension.toLowerCase();
            }
            switch (extension) {
                case 'jpeg':
                case 'jpg':
                case 'jpe':
                case 'gif':
                case 'tif':
                case 'bmp':
                case 'dib':
                case 'ico':
                case 'png':
                case 'pdf':
                case 'tiff':
                case 'jfif':
                    break;
                default:
                    return false;
            }
            var reader = new FileReader();
            reader.onload = (function (file) {
                return function (e) {
                    var binaryData = e.target.result;
                    var base64StringEncoded = window.btoa(binaryData);
                    var fileNameProfile = file.name;
                    var nProfile = fileNameProfile.lastIndexOf(".");
                    var extensionName = fileNameProfile.substring(nProfile + 1, fileNameProfile.length);
                    var attachemnetMime = '{"ContentType":"' + file.type + '","Size":"' + file.size + '","Extension":"' + extensionName + '"}';
                    fileNameProfile = file.name;
                    vm.attachement_mimeProfile = attachemnetMime;
                    vm.fileNameProfile = fileNameProfile;
                    vm.base64StringProfile = base64StringEncoded;
                    vm.srcURL = URL.createObjectURL(file);
                    vm.isLoading = false;
                };
            })(file);
            reader.readAsBinaryString(file);

        },
