                        description: ths.descriptionProfile,
                        onlineUrl: ths.online_url,
                        itemImage: ths.fileNameProfile,
                        giftedByEmialId: '',
                        attendeeId: 0,
                        attachement_mime: ths.attachement_mimeProfile,
                        containerName: ths.ContainerName,
                        base64String: ths.base64StringProfile,
                        moduleName: "Hiring",
                        subModuleCode: "HR_TALENT_POOL",
                        description: ths.fileNameProfile,
                        filetitle: ths.descriptionProfile,
                        Sdoctype: 0,
                        applicantId: ths.RecordId == '0' ? 0 : ths.RecordId,
                        jobname: 0
                    };
                    var postJSON = {
                        data: CustomFieldArray,
                        docsdata: docsdata,
                        overallexpdata: ths.ExpData,
                        educationinfodata: ths.EduInfoData,
                        imagedata: ths.AttachmentData,
                        docsdataProfile: docsdataProfile,
                        jobId: ths.ddljobs==""?0:ths.ddljobs
                    }               
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.AddCandidate(objectPostString).then(function (response) {
                        ths.isLoading = false;
                        if (response.data.status == "Success") {                            
                            if(ths.reqfrom=='HR/CandidateList'){
                                if (ths.RecordId > 0) {
                                    ths.ShowAlert(ths.$t("UpdatedSuccess", [ths.$t("Candidate")]), "success", true, ths.$t("Alert"));
                                }
                                else {
                                    ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("Candidate")]), "success", true, ths.$t("Alert"));
                                }
                                ths.Close();
                            }
                            else if(ths.reqfrom== 'HR/TalentPoolList'){
                                if (ths.RecordId > 0) {
                                    ths.ShowAlert(ths.$t("UpdatedSuccess", [ths.$t("TalentPool")]), "success", true, ths.$t("Alert"));
                                }
                                else {
                                    ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("TalentPool")]), "success", true, ths.$t("Alert"));
                                }
                                ths.Close();
                            }
                            else{
                                if(ths.reqfrom== 'JobPost/JobListing'){
                                    ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("Candidate")]), "success", true, ths.$t("Alert"));
                                    ths.Close();
                                }
                            }
                            // if (ths.isRedirect) {
                            //     window.location.href = "/JobPost/JobDetail?id=" + ths.ddljobs + "&DisplayTab=CandidateDetails";
                            // }                            
                        }
                        else {
                            ths.ShowAlert(ths.$t("AddedError", [ths.$t("Candidate")]), "failure", true, ths.$t("Alert"));
                        }
                    })
                }
            })
        },        
        Close: function () {            
            if(this.reqfrom=='HR/CandidateList'){
                this.$router.push({ name: 'CandidateList' });
            }
            else if(this.reqfrom=='HR/TalentPoolList'){
                this.$router.push({ name: 'TalentPoolList' });
            }else{
                if(this.reqfrom == 'JobPost/JobListing'){
                    this.$router.push({ name: 'JobList' });
                }
            } 
        },
        changeButton: function (index) {                             
                var obj = this;
                if (obj.ExpData[index].Workshere == 0) {
                    obj.ExpData[index].Workshere = 1;
                }
                else {
                    obj.ExpData[index].Workshere = 0;
                }
                $.each(obj.ExpData, function (i) {
                    if (i == index) {
                        this.showName = false;  
                        if (obj.ExpData[index].Workshere == 0) {
                            obj.changeworks(index);
                        }
                    }
                    else {
                        this.Workshere = 0; 
                        this.showName = true; 
                    }
                });
        },
        changeworks: function (index) {
                var obj = this;
                // obj.ExpData[0].ToDateOptions.maxDate = new Date(Date.now());
                // obj.ExpData[0].ToDateOptions.minDate = new Date(obj.ExpData[0].FromDate);
                // $.each(obj.ExpData, function (i) {
                //     if (i == index) {
                //         this.showName = true;
                //     }
                // });
        },
        changePursuebutton: function (index) {
            var obj = this;
            if (obj.EduInfoData[index].pursue == 0) {
                obj.EduInfoData[index].pursue = 1;
            }
            else {
                obj.EduInfoData[index].pursue = 0;
            }
            $.each(obj.EduInfoData, function (i) {
                if (i == index) {
                    this.showEduTodate = false; 
                    if (obj.EduInfoData[index].pursue == 0) {
                        obj.changePursue(index);
                    }
                }
                else {
                    this.pursue = 0; 
                    this.showEduTodate = true; 
                }
            });
        },
        changePursue: function (index) {
            var obj = this;
            $.each(obj.EduInfoData, function (i) {
                if (i == index) {
                    this.showEduTodate = true;
                }
            });
        },
    }
}
</script>