<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddFunctionalArea') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        AreaGroupArr: {
            type: Object,
            required: true
        },
        AreaId: {
            type: Object,
            required: true
        },
        IsIndustryTypeId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CompanyId:null,
            Userid:null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Functional Area'),
                            name: "FunctionalArea",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm=this;
        this.FetchData();
        vm.CompanyId= this.GetUserInfo.CompanyId;
        vm.Userid= this.GetUserInfo.ID;
    },
    methods: {
        FetchData: function () {
            var vm = this;
            if(vm.AreaId>0){
                (vm.FormSchema)[0].Data[0].value = vm.AreaGroupArr.row.crm_lead_industry_type;
                (vm.FormSchema)[1].Data[0].value = vm.AreaGroupArr.row.crm_lead_industry_type_desc;
            }
        },
        Close: function () {
            this.$parent.AddAreaPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                userId: vm.Userid,
                companyId: vm.CompanyId,
                name: formfields.FunctionalArea,
                description: formfields.Description,
                parentid: vm.IsIndustryTypeId,
                id: vm.AreaId == null ? 0 : vm.AreaId,
                subModuleCode: "HR_JOB",
                fileName: '',
                moduleName: "Hiring",
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveFunctionalArea(params).then((response) => {
                if (response.data.status == 'Success') {
                        if (vm.AreaId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("FunctionalArea")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("FunctionalArea")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("FunctionalArea")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>