<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ManageFunctionalAreaType') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>

                    </div>
                    <div class="modal-body">
                        <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="Area" :LegendArray="[]"
                            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                            :widgets="[]" IdentityColumn="crm_lead_industry_type_id" :listType="['List']" :ListData="SkillArr"
                            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                            :SearchFields="leftSearchFields" IsSearchFieldsOnTop="true" SearchPosition="top"
                            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" 
                            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'crm_lead_industry_type'">
                                    <a href="#" @click="openAddArea(data)">{{
                                        data.row.crm_lead_industry_type }}</a>
                                </template>
                            </template>
                        </tg-list>
                    </div>
                </div>
            </div>
        </div>
        <AddAreaPopup v-if="AddAreaPopup" :AreaGroupArr="AreaGroupArr" :AreaId="AreaId" :IsIndustryTypeId="IsIndustryTypeId"></AddAreaPopup>
    </div>
</template>
<script>
import DataServices from './../../../services/DataService';
import Schema from './SkilSchema';
import AddAreaPopup from './AddAreaPopup.vue'
export default {
    props: {
        IsIndustryTypeId: {
            type: Object,
            required: true
        },
        AField: {
            type: Object,
            required: true
        },
        AFieldId: {
            type: Object,
            required: true
        }
    },
    components: { AddAreaPopup },
    data() {
        return {
            isLoading:false,
            HeaderText:'',
            AddAreaPopup:false,
            isAddSkill:false,
            listheaderbuttons: Schema.buttons,
            Headers:[{
                "COLUMN_NAME": "crm_lead_industry_type",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Functional Area',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "crm_lead_industry_type_desc",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Description',
                "DISPLAY_ORDER": 2
            }], 
            SortBy:'',          
            skillStringArr:[],
            SortExp:'',
            TotalRecords: 0,
            searchCondition:'',
            PageNumber: 1,
            PageSize:10,
            AreaId:0, 
            AreaGroupArr:[],        
            SkillArr: [],           
            CurrentPage: 1,
            TotalPages: 0,  
            noRecord:'',  
            skillString: '',
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"industrytypedesc",
                isSearch: true,
                value: "",
                listOptions: []
            }],
        }
    },
    created: function () {
        var vm=this;
        vm.FetchData();
    },
    methods: {
        renderActions: function (row) {
           
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddSkill":
                    this.openAddArea();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        openAddArea: function (item) {
            var vm =this;
            if (item == undefined){
                vm.AreaId=0;
                vm.AreaGroupArr = [];
            }else{
                vm.AreaId=item.row.crm_lead_industry_type_id;
                vm.AreaGroupArr = item;
            }
            vm.AddAreaPopup = !vm.AddAreaPopup;
        },       
        commonDeleteData: function (id) {            
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {                   
                    var url=`ids=${val}&tablename=TALYGEN_CRM_LEAD_INDUSTRY_TYPE&key=crm_lead_industry_type_id`;                 
                    DataServices.DeleteItems(url).then(function (response) {                        
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ManageFunctionalAreaType')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('ManageFunctionalAreaType')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData:function(){
            
            var vueObj = this;
            vueObj.isLoading = true;
            if(vueObj.searchCondition !=''){
                var params = `industryId=${parseInt(vueObj.$parent.IsIndustryTypeId)}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&${vueObj.searchCondition}`;
            }else{
                var params = `industryId=${parseInt(vueObj.$parent.IsIndustryTypeId)}&industrytypedesc=&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            } 
            DataServices.ManageFunctionalArea(params).then((response) => {
                vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "crm_lead_industry_type") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });                                
                if(response.data != ''){
                    vueObj.SkillArr = response.data.DATA;
                    vueObj.TotalRecords = response.data.DATA[0].TotalRecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;   
                    vueObj.listheaderbuttons[1].isdisabled = true; 
                    vueObj.noRecord = false;
                }
                else{                    
                    vueObj.noRecord = true;
                    vueObj.SkillArr = [];
                    vueObj.TotalRecords = 0
                   
                }
                vueObj.isLoading = false;
            })
        },
        Close: function () {
            this.$parent.GetDropDownData("HR_FUN_AREA", this.AField, this.AFieldId, null);   
            this.$parent.Isopenaddareapopup = false;
        }
    }
}
</script>